import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { SubnavComponent } from './subnav/subnav.component';
import { MainNavComponent } from './main-nav/main-nav.component';
import { Nav3Component } from './nav3/nav3.component';
import { Nav4Component } from './nav4/nav4.component';
import { AlertModule } from 'ngx-bootstrap';
import {CarouselModule } from 'ngx-bootstrap';
import {FormsModule} from '@angular/forms';
import { NgxSiemaModule } from 'ngx-siema';
import { Ng2CarouselamosModule } from 'ng2-carouselamos';
import { ArtistsComponent } from './artists/artists.component';
import { Artist2Component } from './artist2/artist2.component';
import { TheshopComponent } from './theshop/theshop.component';
import { FaqComponent } from './faq/faq.component';
import { AboutComponent } from './about/about.component';
import { FooterComponent } from './footer/footer.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatButtonModule} from '@angular/material';
import {GalleriaModule} from 'primeng/primeng';
import { Port1imagesComponent } from './port1images/port1images.component';
import { Port2imagesComponent } from './port2images/port2images.component';
import {RouterModule} from '@angular/router';
import {LightboxModule} from 'primeng/primeng';
import {HttpModule} from '@angular/http';
import {MatDialogModule} from '@angular/material';
import {GalleryConfig, GalleryModule} from 'ng-gallery';
import { NgxGalleryModule } from 'ngx-gallery';
import {MatCardModule} from '@angular/material';
import { ThestudioinfoComponent } from './thestudioinfo/thestudioinfo.component';
import { BriefhistoryComponent } from './briefhistory/briefhistory.component';
import { Artist3Component } from './artist3/artist3.component';

export const galleryConfig: GalleryConfig = {
  // ...
}
@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    SubnavComponent,
    MainNavComponent,
    Nav3Component,
    Nav4Component,
    AboutComponent,
    FaqComponent,
    FooterComponent,
    ArtistsComponent,
    Artist2Component,
    TheshopComponent,
    Port1imagesComponent,
    Port2imagesComponent,
    ThestudioinfoComponent,
    BriefhistoryComponent,
    Artist3Component,

  ],
  entryComponents: [
    Port1imagesComponent,
    Port2imagesComponent,
    ThestudioinfoComponent,
    BriefhistoryComponent,
  ],
  imports: [
    BrowserModule,
    AlertModule.forRoot(),
    CarouselModule.forRoot(),
    FormsModule,
    MatCardModule,
    NgxSiemaModule.forRoot(),
    NgxGalleryModule,
    Ng2CarouselamosModule,
    BrowserAnimationsModule,
    MatButtonModule,
    GalleriaModule,
    GalleryModule.forRoot(galleryConfig),
    LightboxModule,
    MatDialogModule,
    HttpModule,
    RouterModule.forRoot([
      { path: 'port1imgs', component: Port1imagesComponent },
      { path: 'port2imgs', component: Port2imagesComponent },
    ])

  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



