import { Component, OnInit } from '@angular/core';
import {MatDialog} from '@angular/material';
import {Port1imagesComponent} from '../port1images/port1images.component';
import {ThestudioinfoComponent} from '../thestudioinfo/thestudioinfo.component';
import {BriefhistoryComponent} from '../briefhistory/briefhistory.component';
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  animationClicked: boolean;
  constructor(public dialog: MatDialog) {}

  ngOnInit() {
  }
  openDialog2() {
    this.dialog.open(ThestudioinfoComponent);
  }
  openDialog1 () {
    this.dialog.open(BriefhistoryComponent);
  }
  onClick() {
    this.animationClicked = true;
  }
}



